import React from 'react';

import db from '@dhcsgo/lib/db';

import { Link } from 'react-router-dom';

import Box from '@silk/Box';
import Text from '@silk/Text';
import Navbar from '@silk/Navbar';

import ArjoonLogo from '@silk/Navbar/arjoon_logo.png';
import CsgoLogo from '@silk/Navbar/csgo_logo.png';

import Dust2Background from './dust2_blurred.png';

const Styles = {
    root: {
        backgroundColor: '#222',
    },
    bannerSection: {
        paddingTop: 96,
        paddingBottom: 72,
        backgroundColor: '#222',
        backgroundImage: `url(${Dust2Background})`,
        backgroundPosition: 'center',
        backgroundSize: 'cover',
        backdropFilter: 'blur(8px)',
    },
    arjoonLogo: {
        width: 80,
        marginLeft: 12,
        marginRight: 12,
    },
    majorSubtitle: {
        backgroundColor: 'red',
        paddingTop: 8,
        paddingBottom: 8,
        paddingLeft: 16,
        paddingRight: 16,
        borderRadius: 999,
        marginTop: 16,
    },
    csgoLogo: {
        width: 64,
        marginTop: 4,
    },
    standingsSection: {
        padding: 32,
        overflowX: 'auto',
    },
    standingsTable: {
        maxWidth: 1000,
        width: '100%',
    },
    teamNameColumn: {
        paddingLeft: 16,
    },
    winsColumn: {
        width: 40,
        alignItems: 'center',
    },
    lossesColumn: {
        width: 40,
        alignItems: 'center',
    },
    tiesColumn: {
        width: 40,
        alignItems: 'center',
    },
    ptsColumn: {
        width: 40,
        alignItems: 'center',
    },
    rdColumn: {
        width: 40,
        alignItems: 'center',
    },
    kdColumn: {
        width: 64,
        alignItems: 'center',
        paddingRight: 8,
    },
    teamRow: {
        backgroundColor: '#404471',
        marginTop: 8,
        cursor: 'pointer',
    },
    match: {
        marginTop: 16,
        backgroundColor: '#404471',
        paddingTop: 32,
        paddingBottom: 32,
    },
    spectateBox: {
        maxWidth: 1000,
        width: '100%',
        backgroundColor: '#888',
        borderRadius: 4,
        paddingLeft: 4,
        paddingRight: 4,
        paddingTop: 8,
        paddingBottom: 8,
    },
};


export default function MainScene() {
    const sortedTeams = Object.keys(db.teams).map(id => db.teams[id]).sort((a, b) => a.name > b.name ? 1 : (b.name > a.name ? -1 : 0));
    const currentMatch = null;

    const standings = {};
    for (let teamId of Object.keys(db.teams)) {
        standings[teamId] = { W: 0, L: 0, T: 0, RD: 0, K: 0, D: 0 };
    }

    for (let match of db.rrMatches) {
        if (match.team1Result) {
            standings[match.team1][match.team1Result] += 1;
        }
        if (match.team2Result) {
            standings[match.team2][match.team2Result] += 1;
        }
        if (match.team1Rounds !== undefined && match.team2Rounds !== undefined) {
            standings[match.team1].RD += match.team1Rounds - match.team2Rounds;
            standings[match.team2].RD += match.team2Rounds - match.team1Rounds;
        }
        if (match.team1Kills !== undefined && match.team1Deaths !== undefined) {
            standings[match.team1].K += match.team1Kills;
            standings[match.team1].D += match.team1Deaths;
            standings[match.team2].K += match.team1Deaths;
            standings[match.team2].D += match.team1Kills;
        }
    }

    return (
        <Box style={Styles.root} flex={1}>
            <Navbar />
            <Box style={Styles.bannerSection} direction="column" alignItems="center">
                <Box direction="row" alignItems="center">
                    <Text textStyle="absoluteChungus" solid bold italic style={{textStroke: '5px black'}}>DAPPY</Text>
                    <img src={ArjoonLogo} style={Styles.arjoonLogo} />
                    <Text textStyle="absoluteChungus" solid bold italic>HIWALI</Text>
                </Box>
                {/*<Box>*/}
                {/*<Text textStyle="chungus" solid bold>MAJOR</Text>*/}
                {/*</Box>*/}
                <Box style={Styles.majorSubtitle}>
                    <Text solid bold>JULY 2020 MAJOR</Text>
                </Box>
                <img src={CsgoLogo} style={Styles.csgoLogo} />
            </Box>
            {
                currentMatch !== null && (
                    <>
                    <Box alignItems="center" style={{marginTop: 48, marginBottom: 16}}>
                        <Text textStyle="bigHeadline" bold color="#ffc40a">Live</Text>
                    </Box>
                    <Box style={Styles.standingsSection} id="standings">
                        <Box direction="row" justifyContent="center">
                            <Box style={Styles.standingsTable}>
                                <Box key={currentMatch.id} direction="row" style={Styles.match}>
                                    <Box flex={1} direction="column" alignItems="center" justifyContent="center">
                                        <img style={{width: 80, marginBottom: 8}} src={db.teams[currentMatch.team1].logo} />
                                        <Text textStyle="headline">{db.teams[currentMatch.team1].name}</Text>
                                    </Box>
                                    <Box direction="column" alignItems="center">
                                        <Text uppercase style={{marginBottom: 24}} sub>{currentMatch.name}</Text>
                                        <Text textStyle="headline" bold color="#ffc40a" style={{marginBottom: 24}}>vs</Text>
                                        <Text uppercase style={{marginBottom: 4}} sub>Map: {currentMatch.map}</Text>
                                        <Text uppercase style={{marginBottom: 4}} sub>Date: {currentMatch.time}</Text>
                                    </Box>
                                    <Box flex={1} direction="column" alignItems="center" justifyContent="center">
                                        <img style={{width: 80, marginBottom: 8}} src={db.teams[currentMatch.team2].logo} />
                                        <Text textStyle="headline">{db.teams[currentMatch.team2].name}</Text>
                                    </Box>
                                </Box>
                            </Box>
                        </Box>
                        {/*<Box alignItems="center" style={{marginBottom: 20, marginTop: 24}}>*/}
                            {/*<Text textStyle="headline" bold>Spectate</Text>*/}
                            {/*<Box style={Styles.spectateBox} justifyContent="center" alignItems="center" margin={{top: 16}}>*/}
                                {/*<Text mono>N/A</Text>*/}
                            {/*</Box>*/}
                        {/*</Box>*/}
                    </Box>
                    </>
                )
            }
            <Box alignItems="center" style={{marginTop: 48, marginBottom: 16}}>
                <Text textStyle="bigHeadline" bold color="#ffc40a">Round Robin Stage</Text>
            </Box>
            <Box style={Styles.standingsSection} id="standings">
                <Box alignItems="center" style={{marginBottom: 20}}>
                    <Text textStyle="mediumHeadline" bold>Standings</Text>
                </Box>
                <Box direction="row" justifyContent="center">
                    <Box style={Styles.standingsTable}>
                        <Box direction="row">
                            <Box flex={1} style={{paddingLeft: 8}}><Text secondary>Team</Text></Box>
                            <Box style={Styles.winsColumn}><Text secondary>W</Text></Box>
                            <Box style={Styles.lossesColumn}><Text secondary>L</Text></Box>
                            <Box style={Styles.tiesColumn}><Text secondary>T</Text></Box>
                            <Box style={Styles.ptsColumn}><Text secondary>Pts</Text></Box>
                            <Box style={Styles.rdColumn}><Text secondary title="Round Difference: Rounds won - rounds lost">RD</Text></Box>
                            <Box style={Styles.kdColumn}><Text secondary title="Kills - Deaths">K-D</Text></Box>
                        </Box>
                        {
                            sortedTeams.map(team => (
                                <Link to={`/team/${team.id}`} style={{textDecoration: 'none'}}>
                                    <Box key={team.id} direction="row" style={Styles.teamRow} alignItems="center">
                                        <img src={team.logo} style={{height: 42, width: 42, padding: 8, objectFit: 'contain', backgroundColor: '#111'}} />
                                        <Box direction="row" flex={1}>
                                            <Box flex={1} style={Styles.teamNameColumn}><Text bold>{team.name}</Text></Box>
                                            <Box style={Styles.winsColumn}><Text>{standings[team.id].W}</Text></Box>
                                            <Box style={Styles.lossesColumn}><Text>{standings[team.id].L}</Text></Box>
                                            <Box style={Styles.tiesColumn}><Text>{standings[team.id].T}</Text></Box>
                                            <Box style={Styles.ptsColumn}><Text>{standings[team.id].W * 3 + standings[team.id].T}</Text></Box>
                                            <Box style={Styles.rdColumn}><Text>{standings[team.id].RD}</Text></Box>
                                            <Box style={Styles.kdColumn}><Text>{standings[team.id].K} - {standings[team.id].D}</Text></Box>
                                        </Box>
                                    </Box>
                                </Link>
                            ))
                        }
                    </Box>
                </Box>
            </Box>
            <Box id="matches" style={Styles.standingsSection}>
                <Box alignItems="center" style={{marginBottom: 20}}>
                    <Text textStyle="mediumHeadline" bold>Matches</Text>
                </Box>
                <Box direction="row" justifyContent="center">
                    <Box style={Styles.standingsTable}>
                        {
                            db.rrMatches.map(match => (
                                <Box key={match.id} direction="row" style={Styles.match}>
                                    <Box flex={1} direction="column" alignItems="center" justifyContent="center">
                                        <img style={{width: 80, marginBottom: 8}} src={db.teams[match.team1].logo} />
                                        <Text textStyle="headline">{db.teams[match.team1].name}</Text>
                                    </Box>
                                    <Box direction="column" alignItems="center">
                                        <Text uppercase style={{marginBottom: 24}} sub>{match.name}</Text>
                                        <Text textStyle="headline" bold color="#ffc40a" style={{marginBottom: 24}}>{match.status}</Text>
                                        <Text uppercase style={{marginBottom: 4}} sub>Map: {match.map}</Text>
                                        <Text uppercase style={{marginBottom: 4}} sub>Date: {match.time}</Text>
                                        <Text uppercase sub>Popflash: {match.popflash ? <a href={match.popflash} target="_blank">Link</a> : 'N/A'}</Text>
                                    </Box>
                                    <Box flex={1} direction="column" alignItems="center" justifyContent="center">
                                        <img style={{width: 80, marginBottom: 8}} src={db.teams[match.team2].logo} />
                                        <Text textStyle="headline">{db.teams[match.team2].name}</Text>
                                    </Box>
                                </Box>
                            ))
                        }
                    </Box>
                </Box>
            </Box>
        </Box>
    );
}
